import React, { Component } from 'react';
import './App.css';
import data from './../data/scrape-2019-02-21.json'
import * as d3 from 'd3'

class App extends Component {
  render() {
    const f = d3.format(',')
    return (
      <div className="App">
        <header className="App-header">
          <p>
            {`This project has data for ${f(data.bills.length)} bills and ${f(data.votes.length)} separate votes.`}
          </p>
        </header>
      </div>
    );
  }
}

export default App;
